#playPad {
  background: 
  repeating-radial-gradient(circle at 10% 50%,
    hsla(150, 100%, 70%, 0.5) 1%,
    hsla(50, 100%, 70%, 0.5) 2%,
    hsla(150, 100%, 70%, 0.5) 3%

    );
  padding: 2rem;
  border-radius: 100%;
  box-sizing: border-box;
  max-width: 100%;
}

#playPad .btn {
  color: white;
}